@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{

  font-family: "Varela Round", sans-serif;
  padding:0;
  margin:0;
  
}
.user-table{

    width:70%;
    background: white;
    border-radius: 8px;
    margin:50px  auto;
    padding:50px;
    box-shadow: 1px  1px 8px rgba(0,0,0,0.70);
    display: flex;
flex-direction: column;
grid-gap:20px;
gap:20px




}



.add-button{

text-decoration: none;
background-color: #A42153;
padding:10px;
width: 70px;
border-radius: 6px;
font-size: 1rem;
color:rgb(255,255,255);
font-weight: 500;

}


.table{


    text-align: center;
}

.delete-button{
    margin-right: 1rem;
    padding:5px;
    border-radius: 4px;
    border: none;
    background-color: #A42153;
    color: rgb(255,255,255);
    font-weight: 500;



}


.table thead th{


   padding:10px
}

.table tbody  td{


    padding:10px
 }
 

 .edit-button{


    background-color: lightpink;
    padding:4px;
    border-radius: 4px;
    text-decoration: none;
    color:black;
    font-weight: 500;
 }




form  button{

padding:10px;
margin:5px;
border-radius: 8px;
background-color: #A42153;
color:rgb(255, 255, 255);
font-weight: 500;
font-size: max(1vw,12px);
border: none;

}



.adduser{

    width:40%;
    margin:50px auto;
    box-shadow: 1px  1px 8px rgba(0,0,0,0.70);
    text-align: center;
    border-radius: 5px;
    background-color: white;

}

.add-user-form .inputs{
width: 100%;
display: flex;
flex-direction:  column;
justify-content:  center;
align-items: center;

}


.add-user-form .inputs label{
   
margin-bottom: 5px;


    }
    
.add-user-form .inputs input{
   
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 4px;

 outline: none;
    
    
        }


          
.add-user-form  button{


    background-color: lightcoral;
    color:rgb(255, 255, 255);
    font-size: max(12px,1.5vw);
    padding: 10px 20px;
    font-weight: 500;
    cursor: pointer;
   
  
    
    
    }


.adduser h3{

    font-size: larger;
    font-weight: 700;



    margin:20px
}


.update-user{
    width:40%;
    margin:50px auto;
    box-shadow: 1px  1px 8px rgba(0,0,0,0.70);
    text-align: center;
    border-radius: 5px;
    background-color: white;


}
